.filter-container {
    padding: 2rem 0;
}
.filter-form {
  width: 60vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(202px, 1fr));
  grid-row-gap: 2rem;
  grid-column-gap: 40px;
}
input{
  width: 100%;
  background: transparent;
  font-size: 1rem;
  border-radius: 1rem;
}
